import React from "react";
import {CSSProperties, ReactElement} from "react";
import { standardMargin, rowLayout, responsiveRowLayout} from "./Layout.module.css";


export function StandardPadding({children} : {children: ReactElement | ReactElement[]}) {
    return (
        <div className={standardMargin}>
            {children}
        </div>
    );
}


export function RowLayout({children = [], style = {}} : {children: ReactElement | ReactElement[], style: CSSProperties}) {
    return (
        <div className={rowLayout}
             style={style}>
            {children}
        </div>
    )
}

/**
 * Es una fila que se comporta como columna en dispositivos pequeños
 * @param children
 * @param reversed
 * @constructor
 */
export function ResponsiveRowLayout({children = []} : {children: ReactElement[]}) {
    return (
        <div className={responsiveRowLayout}>{
            children
        }
        </div>
    );
}


export function LayoutGrowSpace() {
    return (
        <div style={{flexGrow: 1}}/>
    );
}