import React from "react";
import { sectionBg, sectionWidth } from "./Section.module.css";
import {CSSProperties, ReactElement} from "react";

export function Section({contentMaxWidth = "1200px" , style = {}, className = "", children = []} : {contentMaxWidth?: string, style?: CSSProperties ,className?: string, children?: ReactElement | ReactElement[]}) {
    return (
        <div className={sectionBg}
        style={style}>
            <div
                className={`${sectionWidth} ${className}`}
                style={{
                    maxWidth: contentMaxWidth
                }}>
                {children}
            </div>
        </div>
    )
}
