import React from "react";
import logoImg from "../../assets/images/logo.png";
import * as styles from "./NavLogo.module.css";
import imageMetadata from "../../assets/images/metadata.json";

export function NavLogo({logoHeightEm = 10} : {logoHeightEm: number}) {

    const logoDimPx = imageMetadata["logo.png"];

    const logoHeight = `${logoHeightEm}em`;
    const logoWidth = `${logoHeightEm / logoDimPx.h * logoDimPx.w}em`;


    const Image = () => <div
        style={{
            width: logoWidth,
            height: logoHeight
        }}>
        <img style={{width: "100%", height: "100%"}} src={logoImg}/>
    </div>;



    return <div className={styles.logoFx} style={{display: "flex"}}>
        <Image/>
    </div>;
}